body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media only screen and (max-width: 769px) {
  figure {
    width: 300px;
    height: 200px;
  }
  .column.column-1 {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .hero-div {
    height: 1000px !important;
    /* flex-direction: initial; */
  }

}

.hero-div {
  /* border: 1px black solid; */
  height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
  /* flex-direction: column; */
  background-image: url("https://dm0qx8t0i9gc9.cloudfront.net/thumbnails/video/cW5lDBG/glowing-light-blue-abstract-programming-code-background-on-dark-software-or-script-development-concept_rgg0rnttyl_thumbnail-1080_01.png");
  background-size: cover;
  background-repeat: no-repeat;
}

.hero-content {
  background-color: rgb(136, 203, 37);
  width: 850px;
  height: 500px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.12), 0 1px 4px rgba(0, 0, 0, 0.24);
  border-radius: 5px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

