.section-box {
  width: 100%;
  border: solid 1px white;
  height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
  
}
.profile {
  width: 700px;
  height: 400px;
  border: 1px solid white;
  background-color: black;
}

.profile.pic {
  height: 200px;
  margin: 0;
}

.column.column-2 {
  border: 1px solid white;
  height: 300px;
  margin: 30px;
  margin-top: 50px;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}

.profile-intro {
  border: 1px solid white;
}

.contact-details {
  border: 1px solid white;
  display: flex;
  justify-content: space-between;
}

.list-email {
  border: 1px solid white;
  margin: 10px;
  display: flex;
  justify-content: flex-start;
}
.list-items {
  border: 1px solid white;
  margin: 10px;
}

ul {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  flex-direction: column;
}


